<template>
    <div class="list-wrap">
        <h1>Products List</h1>

        <div class="menu">
             <multiselect :placeholder="holder" :options="actions" :searchable="false" :reset-after="true" @select="dispatchAction"></multiselect>
        </div>
        <div class="list">
             <LessonItem 
                v-for="lesson in lessons"
                :lesson ="lesson"
            >
            </LessonItem>
        </div>
    </div>
</template>

<script>
    import { mapState,mapActions,mapMutations } from 'vuex';
    import LessonItem from './LessonItem'
    import Multiselect from 'vue-multiselect'


    export default {
        data(){
            return {
                choose:'',
                value:'',
                actions:['all', 'board', 'pants','suit'],
                holder:'Choose',
            }
        },
        components:{
            LessonItem,
            Multiselect
        },
        computed:{
            ...mapState(['lessons','orglessons']),
        },
        methods:{
            ...mapActions(['getAPI']),
            ...mapMutations(['chooseVal']),
            dispatchAction (actionName) {
              this.chooseVal(actionName);
              this.holder = actionName;
            }
        },
        mounted(){
            this.getAPI()
        }
    }
</script>

<style scoped>
    .multiselect {
        width: 30%;
        margin:0 auto;
    }

    .list-wrap {
        padding-top:70px;
        text-align: center;
    }
    .list {
        text-align: center;
        display: flex;
        justify-content:flex-start;
        flex-wrap: wrap;
        width:80%;
        margin:10px auto 0;
    }

    h1 {
        text-align: center;
        font-family: cursive;
        font-style: italic;
    }

</style>
<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>



