t World Qualifying Series events on the tour; and a series of amateur surf contests for young surfers the world over. With such substantial event sponsorship and an ever-expanding team, Enjoysufing is proud to be the largest corporate sponsor of women's surfing worldwide.")]),_vm._v(" "),_c('ul',[_c('li',[_vm._v("Email :"),_c('a',{attrs:{"href":"mailto:james20209@msn.com"}},[_vm._v("Mail us")])]),_vm._v(" "),_c('li',[_vm._v("Phobe :"),_c('a',{attrs:{"href":"tel:12345678"}},[_vm._v("Click to Call!")])])]),_vm._v(" "),_c('img',{attrs:{"src":"https://scontent-tpe1-1.xx.fbcdn.net/v/t31.0-8/25542466_10155330486257058_508963328051753576_o.jpg?_nc_cat=0&oh=2ec8617f7984b7584ad9ad2837460a66&oe=5BB634A4","alt":""}})])])}]
