<template>
    <div class="alert-wrap">
        <div class="title">Sorry!</div>
        <div class="alert-text">{{alertText}}</div>
        <div class="ok" @click="close">OK</div>
    </div>
</template>

<style scoped>
    .alert-wrap {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        border:1px solid rgb(178,178,178);
        text-align: center;
        background-color: rgba(255,255,255,0.5);
        color:rgba(130,130,130,1);
        z-index: 2000;
        display: none;
        font-weight: bold;
        font-family: sans-serif;
    }

    .title {
        background-color:#3edaa8;
        height: 60px;
        line-height: 60px;
    }

    .alert-text {
        height: 100px;
        background-color: rgb(27, 31, 20);
        color: rgb(239, 233, 233);
        line-height: 100px;
    }

    .ok {
        height: 60px;
        line-height: 60px;
        background-color: rgb(27, 31, 20);
        border-top: 2px solid #3edaa8;
        transition:color 0.5s;
    }

    .ok:hover {
        color:white;
        cursor: pointer;
    }
</style>

<script>
    import {mapState} from 'vuex'
    export default {
        computed:{
            ...mapState(['alertText']),
        },
        methods:{
            close(){
                document.querySelector('.alert-wrap').style.display = 'none';
            }
        }
    }
</script>


