<template>
    <div class="lesson">
        <div class="cover" :style="coverStyle"></div>
        <span class="title">{{lesson.title}}</span>
        <div class="flex">
            <span class="price">{{lesson.price}}</span>
            <select v-if="!inCart" @change="selectAmount(num)" v-model="num">
                <option value="" disabled>select</option>
                <option  :num="idx" v-for="(item,idx) in lesson.amount.length" >{{item}}</option>
            </select>
        </div>
        <div :class="btnClass"
            @click="!inCart&&addToCart(lesson);"
        >
        {{btnLabel}}</div>
    </div>
</template>

<script>
    import {mapGetters,mapMutations} from 'vuex'
    export default {
        data(){
            return {
                num:''
            }
        },
        props:['lesson'],
        computed:{
            ...mapGetters(['isLessonInCart']),
            inCart(){
                return this.isLessonInCart(this.lesson);
            },
            //綁定css的class
            btnClass(){
                return {
                    buy:true,
                    bought:this.inCart,
                }
            },
            coverStyle(){
                return {
                    backgroundImage:`url('${this.lesson.cover}')`
                }
            },
            btnLabel(){
                return (
                    this.inCart ?'Already In Cart':'Add To Cart'
                )
            }
        },
        methods:{
            ...mapMutations(['addToCart','selectAmount']),
            selectAmount(num){
                this.lesson.num = num;
                console.log(num)
            },
        }
    }
</script>


<style scoped>
select {
    width: 65px;
    background: white;
    border-radius: 0;
}
.flex {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.lesson{
  display: inline-block;
  width: 23%;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 2px 2px 15px #999;
  margin: 10px;
  vertical-align: top;
}
.cover{
  width: 100%;
  height: 120px;
  background: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
span{
  display: block;
  padding: 6px;
}
.title{
  font-size: .9em;
  height: 30px;
  color: #333;
  text-align: left;
  font-family: cursive;
}
.price{
  font-size: .8em;
  color: #178fac;
}
.price:before{
  content: '$';
}
.teachers{
  font-size: .7em;
  float: right;
  line-height: 1.7em;
  color: #178fac;
}
.buy{
  clear: both;
  text-align: center;
  padding: 6px;
  font-size: .7em;
  background-color: #dee;
  cursor: pointer;
}
.buy:hover{
  background-color: #cee;
}
.buy.bought{
  background-color: #ccc;
}

@media (max-width:1000px){
        .lesson {
            width:30%;
        }
    }

@media (max-width:500px){
    .lesson {
            width:100%;
        }
}
</style>



