<template>
  <div class="main">
    <Alert></Alert>
    <div class="cart-wraper">
      <Cart></Cart>
    </div>
    <Header></Header>
    <router-view></router-view>
    <Footer></Footer>
  </div>
</template>

<script>
  import Alert from './alert.vue';
  import Cart from './Cart.vue';
  import Header from './Header.vue';
  import Footer from './Footer.vue';
  import {mapMutations} from 'vuex';
  export default {
    methods:{
      ...mapMutations(['showOff'])
    },
    components:{
      Alert,
      Header,
      Footer,
      Cart
    },
  }

</script>

<style>
html, body{
  font-family: Microsoft JhengHei;
  padding: 0;
  margin: 0;
}

.main {
  position: relative;
}

.cart-wraper {
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  z-index: 900;
}


.multiselect__option--highlight:after {
    content:'' !important;
    background: #41b883;
    color: #fff;
}

.stopScroll {
  overflow: hidden;
}
</style>




