import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
//
const URL = 'https://monkeywu.github.io/cart_data.json/';
const store = new Vuex.Store({
  state:{
    //商品存放陣列
    lessons:[],
    //因會有陣列排序更改陣列，故要有一個陣列保留原樣
    orglessons:[],
    //存放放入購物車內的商品
    cart:[],
    show:false,
    //存放每項商品目前庫存
    orgAmount:[],
    //alert顯示字樣
    alertText:'',
  },
  mutations:{
    //在購物車內增加數量，當數量大於庫存跳出alert視窗
    add(state,idx){
      if(parseInt(state.cart[idx].num) + 1 > state.orgAmount[idx].length){
        console.log(parseInt(state.cart[idx].num) + 1);
        console.log(state.orgAmount[idx])
        state.alertText = 'amount not enough';
        document.querySelector('.alert-wrap').style.display = 'block';
        return false;
      }
      state.cart[idx].num = parseInt(state.cart[idx].num) + 1;
      state.cart.push();
    },
    //在購物車內減少數量，當數量小於庫存跳出alert視窗
    minus(state,idx){
      if(state.cart[idx].num <= 1){
        state.alertText = 'can not less than 1';
        document.querySelector('.alert-wrap').style.display = 'block';
        return false;
      }
      state.cart[idx].num = parseInt(state.cart[idx].num) - 1;
      state.cart.push();
    },
    //將購物車內容顯示出來，並阻擋body scrollbar
    showUp(state){
      state.show = true;
        document.querySelector('.cart-wraper').style.display = "block";
        document.querySelector('body').classList.add('stopScroll')
    },
    //將購物車內容隱藏，並恢復body scrollbar
    showOff(state){
      state.show = false;
      document.querySelector('.cart-wraper').style.display = "none";
      document.querySelector('body').classList.remove('stopScroll')
    },
    //從URL取得商品資料後，將資料放入陣列
    setLessons(state,lessons){
      state.lessons = lessons;
      state.orglessons = lessons;
    },
    //將選擇商品放入陣列，如沒選擇數量會alert
    addToCart(state,lesson){
      if(lesson.num === undefined){
        state.alertText = 'please choose amount';
        document.querySelector('.alert-wrap').style.display = 'block';
        return false;
      }
      state.orgAmount.push(lesson.amount);
      state.cart.push(lesson);
      console.log(state.cart);
    },
    //選擇商品種類
    chooseVal(state,item){
      state.lessons = state.orglessons;

      if(item === 'all'){
        state.lessons = state.orglessons
      }

      if(item === 'board') {
        state.lessons = state.lessons.filter(item=>item.class === "board")
      }

      if(item === 'pants') {
        state.lessons = state.lessons.filter(item=>item.class === "pants")
      }

      if(item === 'suit') {
        state.lessons = state.lessons.filter(item=>item.class === "suit")
      }
    },
  },
  getters:{
    //購物車內商品總數量
    lessonsInCart(state,cart){
      return state.cart.reduce((acc,lesson)=> {
        return parseInt(acc) + parseInt(lesson.num);
      },0)
    },
    //購物車內商品總價格
    priceInCart(state,cart){
      return state.cart.reduce((acc,lesson)=> {
        return parseInt(acc) + parseInt(lesson.price)*parseInt(lesson.num);
      },0)
    },
    //判斷商品是否已經在購物車內
    isLessonInCart: state => lesson => (
      state.cart.findIndex(({id}) => (
        id === lesson.id
      )) !== -1
    ),
  },
  //取得商品資料
  actions:{
    getAPI({commit}){
      fetch(URL)
      .then(rs => rs.json())
      .then(rs => {
        commit('setLessons',rs);
        console.log(rs)
      });
    },
  },
})

export default store;


