<template>
    <div class="cart-wrap">
        <div class="cart-detail">
            <span>items in cart：</span>
            <span class="lessons">{{lessons}}</span>
            <span>total</span>
            <span class="price">${{price}}</span>
        </div>
        <br>
        <div>
            <div v-for="(item,idx) in cart" class="cartitem">
                <div class="flex">
                    <div>
                        <img :src="item.cover" alt="" width="100"/>
                    </div>
                    <div>
                        <div class="title">{{item.title}}</div>
                        <div class="price">${{item.price}}</div>
                    </div>
                </div>
                <div style="text-align:center;margin:5px 0">
                    <span @click="add(idx);">+</span> amount:{{cart[idx].num}} <span @click="minus(idx);">-</span>
                </div>
                <div @click.stop="remove(idx)" class="inner-close">delete this item</div>
            </div>
             <div class="close" @click="showOff()">CLOSE</div>
        </div>

       
    </div>
</template>

<script>
    import {mapGetters,mapState,mapMutations} from 'vuex'
    export default {
        computed:{
            ...mapState(['cart','show','orgAmount']),
            ...mapGetters({
                lessons:'lessonsInCart',
                price:'priceInCart',
            }),
        },
        methods:{
            remove(idx){
                this.cart.splice(idx,1);
                this.orgAmount.splice(idx,1);
            },
            ...mapMutations(['showOff','add','minus']),
        }
    }
</script>

<style scoped>
span {
    cursor: pointer;
}
.flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.close {
    position: absolute;
    top: 8px;
    border-bottom: 1px solid rgb(172,178,178);
    right: 20px;
    text-align:center; 
    cursor: pointer;
    transition: color 0.3s;
}
.close:hover {
    color:rgb(187, 183, 183);
    
}
.cart-wrap{
      position: fixed;
    width: 60%;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 6px 8px;
    box-sizing: border-box;
    background-color: #fff;
    font-size: .9em;
    color: #000;
    font-family: monospace;
    height: 400px;
    overflow: scroll;
    border: 1px solid #000;
    border-radius: 10px;
}
.cartitem {
    margin-bottom: 5px;
    border-bottom:1px solid rgb(187, 183, 183);
    width:80%;
    margin:0 auto 15px;
    text-shadow: none;
    font-size: 14px;
    font-family: cursive;
}

.inner-close {
    text-align: center;
    margin-bottom: 5px;
    transition:all 0.5s;
    cursor: pointer;
}

.inner-close:hover {
    color:rgb(187, 183, 183);
}

@media (max-width:500px) {
    .close {
        position: relative;
        bottom:15px;
        right: 0;
        top: 0;
        border-bottom: none;
    }
}
</style>


