<template>
    <div class="header">
        <div class="menu">
            <router-link to='/home'>Home</router-link>
            <router-link to='/products'>Products</router-link>
            <router-link to='/about'>About</router-link>
        </div>

        <div class="login">
            <a href="" @click.prevent="login" v-if="!connected">FB Login</a>
            <span v-else class="phone">HI! {{username}}</span>
            <img :src="imgSrc" alt="" class="phone phoneimg">
            <div class="cart-inner">
                <p @click="showUp()">Cart ({{lessons}})</p>
            </div>
            <p @click="logout" v-if="connected">LogOut</p>
        </div>

        <div class="phone-menu">
            <div @click="phone_menu = true">
                <div class="hamburger"></div>
                <div class="hamburger"></div>
                <div class="hamburger"></div>
            </div>
        </div>

        <div class="phone-menu-list" v-if="phone_menu">
            <router-link to='/home'>Home</router-link>
            <router-link to='/products'>Products</router-link>
            <router-link to='/about'>About</router-link>
            <div class="close" @click="phone_menu = false">X</div>
        </div>
    </div>
    
</template>

<script>
    import {mapMutations,mapGetters} from 'vuex';
    export default {
        computed:{
          ...mapGetters({
              lessons:'lessonsInCart'
          }),
        },
        data(){
            return {
                connected:false,
                username:'',
                imgSrc:'',
                phone_menu:false,
            }
        },
        mounted(){
                //FB登入初始化，提供dev id跟版本
                FB.init({
                    appId: 369170323592519,
                    version: 'v2.12'
                });
                // Get FB Login Status
                FB.getLoginStatus(this.statusGet);
        },
        methods:{
            ...mapMutations(['showUp','showOff']),
            //FB登入
            login(){
                FB.login(this.statusGet);
            },
            //取得登入狀態，如成功拿取使用者圖片跟名稱
            statusGet(result){
                if(result.status === 'connected'){
                     this.connected = true;
                     FB.api('/me','GET',{
                         fields:['picture','name'],
                     },rs=>{
                         this.username = rs.name
                         this.imgSrc = rs.picture.data.url
                     })
                }
            },
            //FB登出及重新整理頁面
            logout(){
                FB.logout(rs=>document.location.reload());                
            }
        }
    }
</script>

<style scoped>

    .cart-inner {
        cursor: pointer;
    }
    .login {
        width:30%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: cursive;
    }
    .login a,.login p{
        margin: 0 5px;
        color:white;
        transition: all 0.5s;
        cursor: pointer;
        padding:5px;
    }
    .login span {
        color:white;
        margin:0 5px;
    }
    .header {
        display: flex;
        justify-content: center;
        align-items: center;
        padding:20px 0;
        background-color: black;
        position: fixed;
        top: 0;
        width:100%;
        z-index: 1000;
    }
    .menu {
        font-family: Microsoft JhengHei;
        text-align: center;
        width:70%;
    }

    a {
        color:white;
        text-decoration: none;
        font-size: 16px;
        transition: color 0.5s;
        padding:5px;
        margin:0 10px;
        display: inline-block;
        font-family: cursive;
        font-style: italic;
    }

    a:hover,p:hover {
        color:rgb(183, 180, 180);
    }

    .login img {
        border-radius: 100%;
        margin: 0 5px;
    }

    .hamburger {
        width: 35px;
        height: 5px;
        background-color: white;
        margin: 6px 0;
        cursor: pointer;
    }

    .phone-menu {
        position: absolute;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
        display: none;
    }

    .phone-menu-list {
        position: fixed;
        left:0;
        top:10px;
        background-color:black;
    }
    .phone-menu-list a {
        display: block;
        width: 300px;
        text-align: center;
        height: 50px;
        line-height: 50px;
        margin:40px 20px;
        border-bottom:1px solid white;
        transition: all 0.5s;
    }    
    
    .phone-menu-list a:hover {
        border-bottom:1px solid rgb(183, 180, 180);
    }
    .close {
        color:white;
        position: absolute;
        top: 5px;
        right: 15px;
        cursor: pointer;
        border:1px solid white;
        width:24px;
        height: 24px;
        border-radius: 100%;
        text-align: center;
    }
    
    .cart_inner_hover:hover .cart-wrap{
        display: block;
    }

    .phone_show {
        display: block !important;
    }

    @media (max-width:1000px){
        .login,.menu {
            width:50%;
        }
    }

    @media (max-width:500px){
        .menu {
            display: none;
        }
        .login{
            width:100%;
        }

        .phone-menu {
            display: block;
        }

        .phoneimg {
            display: none;
        }

    }
</style>



